import express from 'express';
import pkg from 'body-parser';
import pool from './db/dbconnect.js';
import cors from "cors";
const { json, urlencoded } = pkg;
const app = express();



app.use(cors({
    origin: '*'
}))
app.use(json())
app.use(urlencoded({
    extended: false
}))

app.get('/list', async (req, res) => {
    const client = await pool.connect();
    let q = await client.query('select * from contract_data')
    res.json(q.rows)
})

app.post('/post', async (req,res) => {
    let { title, contract_agg, whose_created, status } = req.body;
    console.log(req.body)
    try {
        const client = await pool.connect();
    
        let query = await client.query(`insert into public.contract_data(title,contract_agg,whose_created,status) values ('${title}','${contract_agg}','${whose_created}','${status}')`)
        console.log(query)
        res.json({
            success: query.rowCount,
            message: "Record is inserted successfully"
        })

    } catch(e) {
        res.send(e)
    }
    
})


app.listen('3000', () => {
    console.log(`Server is started`)
});